import * as React from 'react';

interface IInputProps {
    className: string;
    type: string;
    value: string;
    onChange: (e: React.ChangeEvent<HTMLInputElement>) => void;
}

export const Input: React.FC<IInputProps> = ({
    className,
    type,
    value,
    onChange,
}: IInputProps) => (
    <input
        className={className}
        type={type}
        value={value}
        placeholder="Search fro gifs and images"
        onChange={onChange}
    />
);